
.frames__menu{

    li{
        position: relative;
        &:nth-child(1){
            border-left: 1px solid #DCDCDC;
            &::before{
                content: "";
                position: absolute;
                width: 100%;
                height: 8px;
                bottom: 0;
                left: 0;
                background: $primary;
            }
        }
        width: 33.33%;
        text-align: center;
        border-right: 1px solid #DCDCDC;
        // border-top: 1px solid #DCDCDC;
        // border-bottom: 1px solid #DCDCDC;
    }
    a{
        color:$black-900;
        font-size: 32px;
        padding: 24px 0px;

    }
}
.frames__header{
    img{
        width: 50%;
    }
}

// optical product start

.optical{
    padding-top: 80px ;
    padding-bottom: 80px;

    h5{
        margin-bottom: 32px;
    }
}
.optical__product{
    display: flex;
    flex-wrap: wrap;

    li{
        width: calc((100% - (24px*3))/4);
        margin-right: 24px;
        margin-bottom: 80px;
        &:nth-child(4n){
            margin-right: 0;
        }
    }
}
.product__content{
    display: flex;
    justify-content: space-between;
    p{
        font-size: 24px;
        &:nth-child(2){
            color: $primary;
            font-weight: 500;
        }
    }
}
.product__color{
    position: relative;

    &::before,&::after{
        content: "";
        position: absolute;
        border-radius: 4px;
        width: 24px;
        height: 24px;
        background: $primary;
    }
    &::before{
        left: 0px;
        top: 0px;
        background-color: #5F3E2D;
    }
    &::after{
        left: 32px;
        top: 0;
        background-color: #B75929;
    }
}
// optical rwd start
@media (max-width:992px) {
    .optical__product{
        li{
          width: calc((100% - (24px*1))/2);;
            margin-right: 24px;
           &:nth-child(4n){
            margin-right: 0px;
           } 
          &:nth-child(2n){
            margin-right: 0px;
          }
        }
    }
    
}

// optical product end
.pagination{
   
    li{
        &:nth-child(1){
            border-left: 1px solid $pagination-color;
            font-size: 19px;
        }
        &:nth-last-child(1){
            font-size: 19px;
            background-color:#F2F2F2;
        }
        &:nth-child(3){
            a{
            background: $black-900;
            color: $white;
            }
        }
        border-right: 1px solid $pagination-color;
        border-top: 1px solid $pagination-color;
        border-bottom: 1px solid $pagination-color;
    }
    a{
        padding:12px 16px ;
        font-size: 18px;
        color:$pagination-color;
        transition: all .3s;
      
        
        &:hover{
            background-color:$black-900;
            color:$white;
        }

    }
}





